/*
 * A comparision tree will layout each attribute of a json deep through its heirarchy with given visual queues
 * that represents a deletion, adition, or change of attribute from the other tree. The status of each node is 
 * evaluated by the parent comparision tool.
 */
import {
  Component,
  OnInit,
  Input,
  Output,
  HostListener,
  ElementRef,
  Renderer,
  ViewChild,
  EventEmitter
} from '@angular/core';

import { InToPipe } from 'into-pipes';

import {
  DragDropPolicy,
  Selectionpolicy,
  EditPolicy
} from '../interfaces/tagbox.interfaces';

import { TagTransfer } from './tag.transfer';

@Component({
  selector: 'tag',
  template: `
<span #selector
    *ngIf="!editMode && removable && isSelectable()" 
    tabindex="0" 
    class="selection fa"
    (click)="select()"></span>
<input 
    *ngIf="editMode" 
    class="editor" 
    (blur)="tabout($event)"
    (keyup)="edit($event)" 
    [value]="name"
    [attr.maxlength]="maxlength"
    [attr.placeholder]="placeholder" #editor/>
<div class="autocomplete off" *ngIf="editMode && autocomplete" #filler>
  <ul>
      <li *ngFor="let x of fillerList; let i = index" 
        (click)="selectedFiller = i" 
        [class.selected]="selectedFiller === i"
        [textContent]="x"></li>
  </ul>
</div>
<span 
    *ngIf="!editMode" 
    class="holder" 
    [innerHTML]="placeholder ? placeholder : formattedName()"></span>
<span 
    *ngIf="!editMode && removable" 
    tabindex="0" 
    class="remove fa fa-times" 
    (click)="remove()"></span>
<span 
    *ngIf="!removable" 
    class="placeholder fa fa-plus-circle"></span>`,
  styles: [`:host{
  cursor:pointer;
  color:#fdfdfd;
  margin:4px 2px;
  display:inline-block;
  background-color:#1F84AB;
  border:1px solid #015E85;
  border-radius:8px 20px 20px 8px;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  padding:3px 0;
  position:relative; }
  :host ::ng-deep img{
    height:25px; }
  :host.left-padded{
    padding-left:8px; }
  :host.drag-over{
    background-color:#add8e6 !important;
    cursor:move; }
  :host[placeholder]{
    background-color:transparent;
    color:#000;
    border:0; }
    :host[placeholder]:hover{
      background-color:#eee !important; }
    :host[placeholder] .editor{
      color:#000; }
  :host:hover{
    background-color:#027912 !important;
    border-color:#024b0b !important; }
  :host.focused{
    background-color:#027912 !important;
    border-color:#024b0b !important; }
  :host.selected:hover{
    background-color:#D6534E; }
  :host.selected{
    background-color:#D6534E; }
    :host.selected .selection.fa:before{
      content:"\\f00c" !important; }
  :host .selection{
    background-color:transparent;
    float:left;
    margin-right:3px;
    padding:0;
    width:10px;
    height:10px;
    font-size:0.8rem;
    padding:5px 3px; }
    :host .selection.fa:before{
      content:"\\f013"; }
  :host .editor{
    background-color:transparent;
    overflow:unset;
    max-width:inherit;
    width:inherit;
    color:#fff;
    border:none; }
  :host .placeholder{
    color:#888585;
    float:right;
    font-size:1rem;
    height:20px;
    line-height:20px;
    margin-left:5px;
    text-align:center;
    width:20px; }
  :host .remove{
    float:right;
    font-size:0.7rem;
    height:20px;
    width:20px;
    color:#fff;
    text-align:center;
    margin-left:5px;
    line-height:20px;
    font-weight:bolder; }
  :host .holder{
    pointer-events:none;
    -webkit-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none; }
  :host .autocomplete{
    position:absolute;
    top:26px;
    z-index:5; }
    :host .autocomplete.off{
      display:none; }
    :host .autocomplete ul{
      border:1px solid #024b0b;
      border-top:0;
      list-style:none;
      background-color:#027912;
      list-style-position:inside;
      margin:0;
      max-height:150px;
      overflow-y:auto;
      padding:0; }
      :host .autocomplete ul li{
        color:#fdfdfd;
        padding:5px;
        white-space:nowrap; }
        :host .autocomplete ul li.selected{
          background-color:#D6534E; }
        :host .autocomplete ul li:hover{
          background-color:#0446a8;
          color:#fff; }
`],
})
export class TagComponent implements OnInit {
  editMode: boolean;

  originalName: string;
  selectedFiller = -1;
  fillerList: string[];

  @Output("onfocus")
  onfocus= new EventEmitter()

  @Output("onchange")
  onchange= new EventEmitter()

  @Output("onselect")
  onselect= new EventEmitter()

  @Output("onremove")
  onremove= new EventEmitter()

  @Output("onadd")
  onadd= new EventEmitter()

  @Output("ondrop")
  ondrop= new EventEmitter()

  @Input("format")
  format: string;

  @Input("removable")
  removable: boolean;

  @Input("maxlength")
  maxlength: string;

  @Input("name")
  name: string;

  @Input("placeholder")
  placeholder: boolean;

  @Input("parent")
  parent: any;

  @Input("autocomplete")
  autocomplete: string[];

  @Input("selectionpolicy")
  selectionpolicy: Selectionpolicy;

  @Input("editpolicy")
  editpolicy: EditPolicy;

  @Input("dragpolicy")
  dragpolicy: DragDropPolicy;

  @ViewChild("editor")
  editor;

  @ViewChild("selector")
  selector;

  @ViewChild("filler")
  filler;

  constructor(
    private dataTransfer: TagTransfer,
    private into: InToPipe,
    public el: ElementRef, 
    private renderer: Renderer
  ){
  }

  ngOnInit() {
    this.init();
    this.el.nativeElement.draggable = (this.dragpolicy !== DragDropPolicy.disabled);
  }

  @HostListener('dragstart', ['$event']) 
  dragStart(event) {
      event.stopPropagation();	
      if (this.allowDrag()) {
        event.dataTransfer.setData("source",this.name); // this is needed to get the darg/drop going..
        this.dataTransfer.setData("source",this); // this is needed because event data transfer takes string not bject
      }
  }
  @HostListener('drag', ['$event']) 
  drag(event) {}
  
  @HostListener('dragend', ['$event']) 
  dragEnd(event) {
      event.stopPropagation();	

      this.renderer.setElementClass(this.el.nativeElement, "drag-over", false);
  }
  @HostListener('drop', ['$event'])
  drop(event) {
    event.preventDefault();
    this.renderer.setElementClass(this.el.nativeElement, "drag-over", false);
    this.ondrop.emit({
      source: this.dataTransfer.getData("source"),
      destination: this
    })
  }
  
  @HostListener('dragenter', ['$event']) 
  dragEnter(event) {
      event.preventDefault();
      if (this.allowDrop(event)) {
          this.renderer.setElementClass(this.el.nativeElement, "drag-over", true);
      } else {
          this.renderer.setElementClass(this.el.nativeElement, "drag-over", false);
      }
  }
  
  @HostListener('dragleave', ['$event']) 
  dragLeave(event) {
      event.preventDefault();
              
      this.renderer.setElementClass(this.el.nativeElement, "drag-over", false);
  }
  
  @HostListener('dragover', ['$event']) 
  dragOver(event) {
      if (this.allowDrop(event)) {
          event.preventDefault();
          this.renderer.setElementClass(this.el.nativeElement, "drag-over", true);
      } else {
          this.renderer.setElementClass(this.el.nativeElement, "drag-over", false);
      }
  }
  
  allowDrop(event): boolean {
      const source = this.dataTransfer.getData("source");

      return (source && source.name != this.name) && this.name && this.name.length > 0;
  }

  allowDrag() : boolean {
    return (this.dragpolicy !== DragDropPolicy.disabled) && this.name && this.name.length > 0;
  }

  @HostListener('keyup', ['$event']) 
  keyup(event) {
    if (event.target === this.el.nativeElement ||
       (this.editor && event.target === this.editor.nativeElement)) {
      const code = event.which;
      if (code === 13) { // cariage return
        this.click(event)
      }else if (code === 9 && this.editMode) { // tab out
        setTimeout(()=>{
          this.editMode = false;
          if (this.originalName.length && this.originalName !== this.name) {
            this.onchange.emit(this);
          } else {
            this.onadd.emit(this);
          }
        }, 66);
      }else if (code === 38) { // arrow up
        if (this.filler) {
          if (this.selectedFiller >= 0) {
            this.selectedFiller--;
          } else {
            this.selectedFiller = this.fillerList.length - 1;
          }         
        }
      } else if (code === 40) { // arrow down
        if (this.filler) {
          if (this.selectedFiller < (this.fillerList.length - 1)) {
            this.selectedFiller++;
          } else {
            this.selectedFiller = -1;
          }   
        }
      }
    } else if (this.selector && event.target === this.selector.nativeElement) {
      const code = event.which;
      if (code === 13) { // cariage return
        if (this.isSelectable()) {
          this.onselect.emit(this)
        }
      }
    } else {
      const code = event.which;
      if (code === 13) { // cariage return
        this.remove();
      }      
    }
  }

  @HostListener('click', ['$event']) 
  click(event) {
    if (this.selector && event.target === this.selector.nativeElement) {
      if (this.isSelectable()) {
        this.onselect.emit(this)
      }
    } else if (this.isEditable()) {
      this.editMode = !this.editMode;
      if (this.editMode) {
        setTimeout(()=>{
          if (this.filler) {
            this.selectedFiller = -1;
            this.updateFillerList(this.name);
          }
          if (this.editor) {
            this.editor.nativeElement.focus();
            if (this.filler) {
              this.renderer.setElementClass(this.filler.nativeElement, "off", false);
            }
          }
        },66);
      } else if (this.selectedFiller >= 0) {
        this.name = this.fillerList[this.selectedFiller];
      }
      if (this.originalName.length) {
        if (this.originalName !== this.name) {
          this.onchange.emit(this);
        }
        if (!this.editMode) {
          setTimeout(()=>{this.el.nativeElement.focus() },66);
        }
      } else {
        this.onadd.emit(this);
      }
    }
  }

  @HostListener('focus', ['$event']) 
  focus(event) {
    if (this.isSelectable()) {
      this.onfocus.emit(this)
    }
  }

  isRemovable() {
    let canRemove = (this.editpolicy === EditPolicy.addAndRemove);

    canRemove = canRemove || (this.editpolicy === EditPolicy.removeOnly);
    
    return  canRemove;
  }

  isEditable() {
    return  (this.editpolicy !== EditPolicy.viewOnly);
  }

  isDraggable() {
    return  (this.dragpolicy !== DragDropPolicy.disabled);
  }

  isSelectable() {
    return  (this.selectionpolicy !== Selectionpolicy.disabled);
  }
  select() {
    
  }

  tabout(event) {
    setTimeout(() => {
      this.name = this.selectedFiller < 0 ? event.target.value : this.fillerList[this.selectedFiller];
      this.editMode = false;
      if (this.originalName.length && this.originalName !== this.name) {
        this.onchange.emit(this);
      } else {
        this.onadd.emit(this);
      }
    }, 66)
  }
  edit(event) {
    this.name = event.target.value;
    this.updateFillerList(this.name);
  }

  updateFillerList(value) {
    if (value && this.autocomplete instanceof Array){
      if (value) {
        this.fillerList = this.autocomplete.filter((item) => item.indexOf(value) >= 0);
      }
    }
  }

  init() {
    this.originalName = this.name;
  }
  reset() {
    this.name = this.originalName;
  }

  remove() {
    if (this.isRemovable()) {
      this.onremove.emit(this);
    }
  }

  formattedName() {
    let result = this.name;
    if (this.format) {
      result = this.into.transform(this.name, this.format);
    }
    return result;
  }

}
